
footer {
    background: #0d1a2b;
    @media (max-width:650px) {
        display: flex;
        flex-direction: column;
        & > * {
            width: 100%;
            margin: 0;
        }
    }
}
.footer {
    &_top {
        border-bottom: 1px solid #1e3046;
        padding-left: 25px;
        padding-right: 25px;
        padding-top: 35px;
        padding-bottom: 28px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #d2d8e8;
        font-size: 14px;
        @media (max-width:830px) {
            flex-wrap: wrap;
        }
        @media (max-width:650px) {
            order: 2;  
            display: block;
            padding-bottom: 10px;
            border: none;
        }
        &_block {
            &:last-child {
                display: flex;
                align-items: center;
                @media (max-width:960px) {
                    display: block;
                    & > div {
                        padding-top: 5px;
                    }
                }
                @media (max-width:830px) {
                    width: 100%;
                    padding-top: 20px;
                }
                @media (max-width:650px) {
                    padding-top: 0px;   
                    display: flex;
                }
                @media (max-width:500px) {   
                    display: block;
                }
                
                a {
                    margin: 0 4px;
                }
            }
            @media (max-width:650px) {
                margin-bottom: 15px;   
            }
        }
        a {
            color: #d2d8e8;
            font-size: 14px;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
        i {
            color: #8a8f9a;
            font-size: 16px;
            margin-right: 12px;
            &.icon-loc {
                font-size: 20px;
            }
        }
        &_center {
            div {
                margin-bottom: 7px;
            }
        }
        
    }
    &_tel {
            a {
                display: block;
                text-decoration: none;
                &:first-child {
                    font-size: 20px;
                    color: #fff;
                    font-weight: 700;
                }
                &:last-child {
                    font-size: 14px;
                    color: #fb4110;
                    font-style: italic;
                    &:hover {
                        text-decoration: underline;
                    }
                    @media (max-width:650px) {
                        display: none;
                    }
                }
            }
    }
    &_center {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 40px;
        @media (max-width:830px) {
            display: block;
        }
        @media (max-width:650px) {
            order: 1; 
            padding-top: 0;
        }
        .btn {
            display: none;
            font-size: 16px;
            padding-left: 0;
            width: 100%;
            margin-top: 20px;
            padding-right: 0;
            @media (max-width:650px) {
                display: block;
            }
        }
    }
    &_nav {
        display: flex;
        justify-content: space-between;
        width: 100%;
        @media (max-width:650px) {
            flex-wrap: wrap;   
        }
    }
    &_a {
        color: #d2d8e8;
        font-size: 16px;
        display: block;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
        & + &_cat {
            margin-top: 15px;
        } 
        & + & {
            margin-top: 20px;
        }
        &_cat {
            display: block;
            color: #8a8f9a;
            text-decoration: underline;
            & + & {
                margin-top: 4px;
            }
            &:hover {
                text-decoration: none;
            }
        }
    }
    &_block {
        width: 30%;
        @media (max-width:830px) {
            margin-bottom: 20px;
        }
        @media (max-width:650px) {
            width: 47%;   
        }
        &:first-child {
            @media (max-width:650px) {
                width: 100%;   
                .footer_a {
                    margin-left: -15px;
                    margin-right: -15px;
                    width: calc( 100% + 30px);
                    padding: 20px 15px 17px 15px;
                    border-bottom: 2px solid #1e3046;
                    display: block;
                    
                    text-transform: uppercase;
                    &:hover {
                        text-decoration: none;
                    }
                    & ~ a {
                        display: none;
                    }
                }
            }
        }
    }
    &_payment {
        color: #8a8f9a;
        padding-bottom: 10px;
        width: 100%;
        max-width: 300px;
        @media (max-width:650px) {
            max-width: 100%; 
            padding-top: 30px;
        }
        div {
            display: flex;
            flex-wrap: wrap;
            margin-left: -5px;
        }
        img {
            margin: 0 5px;
        }
    }
    &_bottom {
        padding-bottom: 22px;
        display: flex;
        justify-content: space-between;
        @media (max-width:650px) {
            order: 3;  
            display: block;
        }
    }
    &_social {
        color: #8a8f9a;
        text-align: right;
        @media (max-width:650px) {
            text-align: left;
            padding-top: 20px;
        }
        b {
            padding-right: 15px;
            padding-bottom: 15px;
            display: inline-block;
            @media (max-width:650px) {
                display: none;
            }
        }
        a {
            display: inline-block;
            margin: 0 7px 0 0;
            font-size: 40px;
            vertical-align: middle;
            color: #8a8f9a;
            text-decoration: none;
            &:hover {
                color: $orange;
            }
        }
        span {
            display: inline-block;
        }
    }
    
}
.copy {
    padding-top: 30px;
    font-size: 10px;
    opacity: 0.3;
    color: #d2d8e8;
    padding-right: 20px;
    @media (max-width:650px) {
        padding-top: 0px;
    }
}
