@import "variables";
@import "mixins";
html,
body {
    font-weight: normal;
    font-family: 'Rubik', sans-serif;
    font-weight: 400;
    height: 100%;
    min-height: 100%;
    @media (max-width:500px) {
        font-size: 14px;
    }
    &.pop_active {
        overflow: hidden;
        margin-right: 17px;
        height: auto;
    }
}
.doors {
    .wrapper {
        max-width: 1160px;
        margin: 0 auto;
        padding-left: 15px;
        padding-right: 15px;
    }

    .section {
        background-repeat: no-repeat;
        background-position: top center;
        background-size: cover;

        position: relative;
        &:not(.nav_section) {
            overflow: hidden;   
        }
        &.slider_product {
            background: #eceff6;
            padding-bottom: 40px;
            &_index {
                .goods_item {
                    width: auto;
                }
                .btn {
                    white-space: nowrap;
                }
                @media (max-width:550px) {
                    .btn_tab_items {
                        display: block;
                    }
                }
            }
            &.card_more_slider {
                background: #fff;
                .goods_item_slider {
                    border: 1px solid #d2d2e2;
                }
            }
            h2 {
                padding-top: 50px;
                padding-bottom: 50px;
                text-transform: uppercase;
                @media (max-width:550px) {
                    display: none;
                }
            }
        }
        &_new {
            padding-bottom: 45px;
            @media (max-width:550px) {
                background: #fff!important;
            }
            h2 {
                text-align: center;
                color: #fff;
                padding: 40px 0 30px 0;
                text-transform: uppercase;
                position: relative;

                @media (max-width:550px) {
                    display: flex;
                    margin-bottom: -20px;
                    width: calc( 100% + 30px );
                    margin-left: -15px;
                    margin-right: -15px;
                    align-items: center;
                    justify-content: space-between;
                    padding-left: 15px;
                    padding-right: 15px;
                    padding-bottom: 50px;
                    background: $blue!important;
                }
            }
        }
        &_blue {
            background: #f3f3fb;
            & + .section.no_lay {
                @media (max-width:860px) {
                    .left {
                        display: none;
                    }
                    .right {
                        width: 100%;
                    }
                }
                @media (max-width:600px) {
                    display: none;
                }
            }
        }
        &_manuf {
            padding-top: 80px;
            padding-bottom: 80px;
            h2 {
                margin-bottom: 25px;
                text-transform: uppercase;
                font-size: 24px;
            }
        }
    }
    .no_lay {
        .right {
            position: relative;
            z-index: 2;
            padding-top: 70px;
    //        padding-bottom: 30px;
            p {
                padding-bottom: 20px;
            }
        }
        &_img {
            position: absolute;
            left: 0;
            top: 0;
            z-index: 0;
            bottom: 0;
            width: auto;
            display: block;
            height: auto;
        }
        .lr {
            &_block {
                align-items: stretch;
            }
        }
    }
    .see {
        &_all {
            color: #d2d8e8;
            font-style: italic;
            font-weight: 300;
            font-size: 14px;
            text-decoration: none;
            position: absolute;
            right: 0;
            bottom: 50%;
            margin-bottom: -15px;
            i {
                display: none;
            }
            @media (max-width:550px) {
                position: relative;
                bottom: auto;
                right: auto;
                margin-bottom: 0;
                span {
                    display: none;
                }
                i {
                    display: block;
                }
            }
            &:hover {
                text-decoration: underline;
            }
        }
    } 

    .lr {
        &_block {
            &:after {
                content: '';
                display: block;
                width: 1px;
                clear: both;
            }
            .left {
                float: left;
                width: 50%;
            }
            .right {
                float: right;
                width: 50%;
                z-index: 2;
                position: relative;
            }
            &_flex {
                display: flex;
                align-items: center;
                justify-content: space-between;
                &:after {
                    display: none;
                }
            }
        }
    }
    .btn {
        position: relative;
        display: inline-block;
        padding: 13px 30px;
        text-transform: uppercase;
        display: inline-block;
        transform: $btn_a;
        text-decoration: none;
        text-align: center;
        span {
            transform: $btn_s;
            display: block;
                white-space: nowrap;
        }
        &.blue {
            background: $blue;
            color: #fff;
            box-shadow: $btn_b_s;
            &:hover {
                @include bg_gr;
            }
        }
        &.orange {
            color: #fff;
            box-shadow: $btn_b_s;
            @include bg_gr;
            &:hover {
                background: $blue;
            }
            &_s {
                background: #fb4110;
            }
        }
        &.no_bg {

            &.active,
            &:hover {
                box-shadow: $btn_b_s;
            }
        }
        &.normal {
            transform: skewX(0deg);
            font-weight: 500;
            span {
                transform: skewX(0deg);
            }
        }
        &.white {
            background: #fff;
            box-shadow: 3px 3px 10px rgba(0,0,0,.35);
            color: $blue;
            font-weight: 700;
            font-size: 20px;
            text-transform: uppercase;
            &:hover {
                @include bg_gr;
                color: #fff;
            }
        }
        &_tab {
            padding: 35px 0 21px 0;
            text-align: center;
            @media (max-width:550px) {
                display: none;
            }
            a {
                text-transform: uppercase;
                font-size: 22px;
                font-weight: 700;
                color: $blue;
                text-decoration: none;
                margin-left: 5px;
                margin-right: 5px;
            }
            &_items {
                display: none;
                &.active {
                    display: block;
                }
            }
        }
    }

    .novelty {
        display: flex;
        @media (max-width:550px) {
            display: block;
        }
        & > .goods_item {
            margin-right: -100px ;
            z-index: 2;
            box-shadow: 0 0 25px rgba(0,0,0,0.1);
            transition: margin-right ease 0.4s;
            @media (max-width:550px) {
                margin-right: 0;
                width: 100%;
                border: 1px solid #d5dae7;
                & + .goods_item {
                    border-top: none;
                }
            }
            &:hover {
                margin-right: 0;
            }
            &:last-child {
                margin-right: 0;
            }
            @media (max-width:700px) {
                &:nth-child(5) {
                    margin-right: 0;
                    & ~ * {
                        display: none;

                    }
                }
            }
        }
    }

    h1 {
        font-size: 22px;
        margin-bottom: 24px;
        @media (max-width:550px) {
            margin-bottom: 0;
        }
        &.h1 {
            display: flex;
            justify-content: space-between;
            align-items: center;
            .view {
                @media (max-width:720px) {
                    display: none;
                }
            }
        }
    }

    .page {
        background: url(../images/bg/page.png) no-repeat top left;
        @media (max-width:600px) {
            background: none;
        }
    }

    .catalog {
        &:not(.page) {
            display: flex;
            justify-content: space-between;
            align-items: stretch;
            @media (max-width:1024px) {
                display: block;   
            }
        }

        h1 {
            margin-bottom: 36px;
        }
        &_block {
            width: 100%;
        }
        aside {
            min-width: 260px;
            width: 260px;
            margin-right: 35px;
            @media (max-width:1024px) {
                position: fixed;
                top: 0;
                bottom: 0;
                left: -330px;
                width: 320px;
                z-index: 100;
                height: auto;
                background: #fff;
                box-shadow: 0 0 5px rgba(0,0,0,.1);
                overflow: hidden;
                overflow-y: auto;

                transition:  left ease 0.4s;
                &.open {
                    left: 0;
                }
            }
        }
        &_in {
            display: flex;
            justify-content: space-between;
            align-items: stretch;
            flex-wrap: wrap;
            .goods_item_tag {
                display: block!important;
                left: auto!important;
                right: -10px!important;
                @media (max-width:550px) {
                    right: auto!important;
                    left: 10px!important;
                }
            }
            .goods_item {
    //            min-width: 250px;
                width: 31%;

                border: 1px solid #cdd0d8;
                margin-bottom: 33px;
                @media (max-width:650px) {
                    width: 48%;
                }
                @media (max-width:550px) {
                    width: 100%;
                }

                &:hover {
                    border-color: #1e3046;
                    box-shadow: 0 0 16px rgba(0,0,0,.39);

                }
                a {
                    &:hover {
                        text-decoration: none;
                    }
                }
                &.goods_item_slider {
                    margin-left: 0;
                    margin-right: 0;
                }
            }
        }
        &_btn {
            text-align: center;
            margin-bottom: 50px;
        }
        &_info {
            padding-bottom: 65px;
            h2 {
                margin-bottom: 30px;
            }
        }
    }

    .logo {
        text-decoration: none;
        i {
            color: #000;
            font-size: 30px;
            vertical-align: middle;
            margin-left: 10px;
    //        text-decoration: none;
        }
        span {
            display: none;
        }
        @media (max-width:930px) {
            img + img {
                display: none;
            }
            span {
                display: inline-block;
                vertical-align: middle;
                width: 32px;
                height: 32px;
                @include bg_gr;
                border-radius: 50%;
                cursor: pointer;
                margin-right: 12px;
                padding-top: 10px;
                i {
                    display: block;
                    width: 14px;
                    height: 2px;
                    border-radius: 5px;
                    background: #fff;
                    margin: 0 auto 3px auto;
                }
            }
        }
        @media (max-width:400px) {
            width: 180px;
            img {
                width: calc( 100% - 54px );
            }
        }
    }
    .mob {
        &_btn {
            &_open {
                position: relative;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 0;
                    height: 0;
                    border: 25px solid transparent;
                    border-bottom-color: #eceff6;
                    left: 50%;
                    margin-left: -25px;
                    bottom: -7px;
                }
            }
        }
    }

    h3 {
        font-size: 16px;
    }

    .conteiner {
        display: flex;
        align-items: stretch;
        justify-content: space-between;
        @media (max-width:780px) {
            display: block;
        }
        &_left {
            width: 100%;
        }
        &_right {
            min-width: 370px;
            width: 370px;
            margin-left: 50px;
            @media (max-width:880px) {
                min-width: 300px;
                width: 300px;
                margin-left: 20px;
            }
            @media (max-width:780px) {
                margin: 55px auto 0 auto;
            }
            @media (max-width:500px) {
                min-width: 100%;
                width: 100%;
            }
        }
        padding-bottom: 50px;
    }
    input {
        &[type="checkbox"] {
            display: none;
                    & + span {
                        display: inline-block;
                        width: 20px;
                        height: 20px;
                        min-width: 20px;
                        min-height: 20px;
                        border-radius: 50%;
                        margin-left: 5px;


                        text-align: center;
                        padding-top: 2px;

                        font-size: 12px;
                        color: rgba(255,255,255,0);

                        background: #fff;
                        border: 1px solid rgba(0,0,0,0.1);
                    }
                    &:checked {
                        & + span {
                            @include bg_gr;
                            color: #fff;
                            border-color: rgba(0,0,0,0)
                        }
                    }
        }         
    }
    //input {
    //    &[type="checkbox"] {
    //        display: none;
    //            & + span {
    //                display: inline-block;
    //                width: 35px;
    //                height: 35px;
    //                border-radius: 50%;
    //                border: 6px solid #eceff6;
    //                //box-shadow: 0 0 0 1px #e7eaf2 inset;
    //            }
    //            &:checked {
    //                & + span {
    //                    box-shadow: none;
    //                    border-color: #fff;
    //                }
    //            }
    //    }
    //            
    //    }

    .thanks_order {
        display: flex;
        justify-content: space-between;
        align-items: center;
        max-width: 360px;
        h2 {
            margin-bottom: 24px;
            font-size: 24px;
            font-weight: 500;
        }
        i {
            &.icon-female {
                font-size: 90px;
                color: #aeb5c1;
            }
        }
    }
    input[type="text"] {
        &.change + span.form_pl,
        &:focus + span.form_pl {
            display: none;
        }
    }
    textarea {
        border: 1px solid #e6e9f1;
        padding: 10px;
        width: 100%;
        min-height: 63px;
        font-size: 14px;
        color: $blue;
        @include plhl() {
            color: $blue;
            font-family: 'Rubik', sans-serif;
            font-size: 16px;
        }
    }
    .back_cat {
        color: #97a2b1;
        font-style: italic;
        margin: 10px 0;
        display: block;
        @media (max-width:400px) {
            text-align: center;
        }
        &:hover {
            text-decoration: none;
        }
    }
.bx-filter {
    form {
        margin: 0!important;
        padding: 0!important;
        background: none!important;
        &:before {
            display: none!important; 
        }
    }
}  
}
