.doors {

    .slider {
        &_main {
            &_item {
                background-size:cover; 
                background-repeat: no-repeat;
                background-position: top center;
                .wrapper {
                    padding-right: 0;
                }
                .right {
                    overflow: hidden;
                    img {
                        margin-top: 10px;
                        display: inline-block;
                        margin-right: 36px;
                        @media (max-width:850px) {
                            margin-right: -45px;   
                        }
                    }
                    @media (max-width:1020px) {
                        .cost {
                            display: none;
                        }   
                    }
                }
                a {
                    text-decoration: none;
                }
                .left {
                    color: #fff;
                    @media (max-width:850px) {
                        width: 80%;
                    }
                    .cost {
                        display: none;
                        @media (max-width:1020px) {
                            display: block;
                            margin-bottom: 15px;
                        }
                         @media (max-width:600px) {
                            b {
                                font-size: 30px;
                            }
                             span {
                                font-size: 15px;
                            }
                        }
                    }
                }
                .h1 {
                    font-size: 50px;
                    text-transform: uppercase;
                    font-weight: 700;
                    margin-bottom: 10px;
                    @media (max-width:600px) {
                        font-size: 25px;
                    }
                }
                p {
                    font-size: 22px;
                    margin-bottom: 75px;
                    @media (max-width:1020px) {
                        margin-bottom: 15px;   
                    }
                    @media (max-width:600px) {
                        font-size: 16px;
                    }
                }
                .btn {
                    margin-left: -27px;
                    @media (max-width:1200px) {
                        margin-left: 0;
                    }
                    @media (max-width:400px) {
                        span {
                            font-size: 12px;
                        }
                    }

                }
            }
            .cost {
                color: $blue;
                text-decoration: none;
                b {
                    font-size: 50px;
                }
                span {
                    display: inline-block;
    //                vertical-align: bottom;
                    font-size: 30px;
                }
            }

        }
        &_prod {
            @media (max-width:1245px) {
                padding-left: 60px;
                padding-right: 60px;
            }
            @media (max-width:550px) {
                padding-left: 0px;
                padding-right: 0px;
            }

            .owl {
                &-nav {
                    button {
                        display: block;
                        position: absolute;
                        top: 50%;
                        margin-top: -13px;
                        &:after {
                            content: "\e915";
                            font-family: 'icomoon' !important;
                              speak: none;
                              font-style: normal;
                              font-weight: normal;
                              font-variant: normal;
                              text-transform: none;
                              line-height: 1;

                              /* Better Font Rendering =========== */
                              -webkit-font-smoothing: antialiased;
                              -moz-osx-font-smoothing: grayscale;
                            font-size: 22px;
                            color: #aeb5c1;
//                            transform: rotate(-90deg);
//                            display: block
                        }
                        &:hover {
                            &:after {
                                color: $orange;
                            }
                        }
                        span {
                            display: none;
                        }
                    }
                }
                &-prev {
                    transform: rotate(90deg);
                    left: -52px;
                    @media (max-width:1245px) {
                        left: 0;   
                    }
                }
                &-next {
                    transform: rotate(-90deg);
                    right: -52px;
                    @media (max-width:1245px) {
                        right: 0;   
                    }
                }
            }
            &_head {
                padding: 15px 10px;
                border: 1px solid #d5dae7;
                display:  none;

                justify-content: space-between;
                align-items: center;
                text-decoration: none;
                font-weight: 700;
                color: $blue;

                text-transform: uppercase;
                font-size: 18px;
                background: #fff;
                margin-top: 25px;
                display: none;
                @media (max-width:550px) {
                    display: flex;   
                }
            }
            &_bottom {
                display: none;
                justify-content: space-between;
                align-items: center;
                padding: 15px 10px;
                border:  1px solid #d5dae7;
                border-top: none;
                background: #fff;
                @media (max-width:550px) {
                    display: flex;   
                }
                &.buy_block_nav {
                    background: #f3f3fb;
                    border: none;
                    border-top: 1px solid #d5dae7;
                    @media (max-width:750px) {
                        display: flex;   
                    }
                }
            }
            &_left {
                transform: rotate(180deg);
                display: block;
            }
            @media (max-width:550px) {
                .goods_item {
                    width: 100%;
                    border: 1px solid #d5dae7;
                    border-top: none;
                }
            }
        }
        &_manuf {
            @media (max-width:1245px) {
                padding-left: 60px;
                padding-right: 60px;
            }
            .owl {
                &-nav {
                    button {
                        display: block;
                        position: absolute;
                        top: 50%;
                        margin-top: -13px;
                        &:after {
                            content: "\e915";
                            font-family: 'icomoon' !important;
                              speak: none;
                              font-style: normal;
                              font-weight: normal;
                              font-variant: normal;
                              text-transform: none;
                              line-height: 1;

                              /* Better Font Rendering =========== */
                              -webkit-font-smoothing: antialiased;
                              -moz-osx-font-smoothing: grayscale;
                            font-size: 22px;
                            color: #aeb5c1;
                            
                        }
                        &:hover {
                            &:after {
                                color: $orange;
                            }
                        }
                        span {
                            display: none;
                        }
                    }
                }
                &-prev {
                    transform: rotate(90deg);
                    left: -52px;
                    @media (max-width:1245px) {
                        left: 0;   
                    }
                }
                &-next {
                    transform: rotate(-90deg);
                    right: -52px;
                    @media (max-width:1245px) {
                        right: 0;   
                    }
                }
            }
            &_item {
                text-align: center;
                img {
                    display: inline-block;
                }
            }
        }
    }

    .goods {
        &_item {
            background: #fff;
            padding: 22px 18px 10px 18px;
            width: 265px;
            position: relative;
            &_slider {
                padding: 22px 18px 18px 18px;
                border-bottom: 3px solid #d7d9e0;
                background: #fff;
                margin-left: auto;
                margin-right: auto;
                @media (max-width:550px) {
                    display: flex;
                    align-items: stretch;
                    .goods_icon {
                        display: none;
                    }
                }
                a {
                    display: block;
                }
                .goods {
                    &_img {
                        margin-bottom: 10px;
                        @media (max-width:550px) {
                            margin-bottom: 0;
                            margin-right: 15px;
                            display: flex;
                            align-items: flex-end;
                            img {
                                width: 100%;
                            }
                            & + span {
                                width: 100%;
                                display: flex;
                                flex-direction: column;
                                justify-content: space-between;
                            }
                        }
                    }
                    &_item_tag {
                        right: auto;
                        left: 10px;
                        display: none;
                        @media (max-width:550px) {
                            display: block;   
                        }
                    }
                }

            }
            a {
                color: $blue;
                font-size: 16px;
                display: block;
            }
            &_tag {
                position: absolute;
                top: 10px;
                right: -10px;
            }
            &_mob {
                display: none!important;
                @media (max-width:550px) {
                    display: block!important;
                }
                &_bottom {
                    @media (max-width:550px) {
                        display: flex;
                        justify-content: space-between;
                        align-items: center;
                        .goods_cost {
                            color: $blue!important;
                        }
                        .btn.normal.blue {
                            min-width: 40px;
                            min-height: 40px; 
                            width: 40px;
                            height: 40px;
                            padding: 0;
                            border-radius: 50%;
                            @include bg_gr;
                            box-shadow: none;
                            padding-top: 9px;
                            font-size: 20px;
                            i,
                            span {
                                display: none;
                            }
                            &:after {
                                content: "\e90f";
                                font-family: 'icomoon' !important;
                              speak: none;
                              font-style: normal;
                              font-weight: normal;
                              font-variant: normal;
                              text-transform: none;
                              line-height: 1;

                              -webkit-font-smoothing: antialiased;
                              -moz-osx-font-smoothing: grayscale;
                            }
                        }
                    }
                }
            }
            &_cat {
                font-size: 13px!important;
                color: #aab2c8!important;
                margin-bottom: 10px;
                &:hover {
                    text-decoration: none;
                }
            }
            .rev {
                margin-top: 10px;
            }
        }
        &_img {
            margin-bottom: 18px;
            img { 
                display: block;
                margin: 0 auto;
            }
        }
        &_cost {
            color: $orange!important;
            text-decoration: none;
            padding: 14px 0 20px 0; 
            font-weight: 700;
            font-size: 20px;
            display: block;

            span {
                font-weight: 400;
            }
        }
        &_icon {
            //padding-top: 30px;
            display: flex;
            align-items: flex-end;
            color: $blue;
            height: 60px;
            padding-top: 5px;
            flex-wrap: wrap;
            div {
                display: block;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                padding: 0;
                padding-top: 5px;
                padding-bottom: 5px;
                & + div {
                    padding: 0;
                    padding-left: 2px;
                    padding-right: 2px;
                    display: inline-block;

                }
                &:not(.goods_sale) {
                    width: 100%;
                }
            }
            a {
                text-decoration: none;
                font-size: 24px;
                margin-right: 12px;
            }
        }
        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: -10px;
            .btn {
                padding-left: 20px;
                padding-right: 20px;
                font-size: 20px; 
            }
        }
        &_sale {
            position: relative;
            &:after {
                content: '';
                display: block;
                left: 0;
                right: 0;
                height: 1px;
                background: #fb4110;
                top: 50%;

                position: absolute;
            }
        }
    }

    h2 {
        font-size:22px;
        font-weight: 700;
        a {
            &:not(.see_all) {
                text-decoration: none;
                color: $blue;
                border-bottom: 2px dashed $blue;
                &:hover {
                    border-color: rgba(255,255,255,0);
                }
            }
        }
    }

    .icon {
        &_block {
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width:810px) {
                flex-wrap: wrap;
            }
            img {
                width: 100%;
    //            height: auto;
                max-width: 257px;

            }
        }
        &_item {
            padding: 20px 0;
            text-align: center;
            color: $blue;
            font-size: 16px;
            font-weight: 700;
            @media (max-width:810px) {
                width: 50%;
            }
        }
    }

    .interior_doors {
        margin-top: 108px;
        padding-bottom: 20px;
        .right {
            padding-top: 61px;
    //        padding-bottom: 80px;
            h2 {
                margin-bottom: 34px;
            }
        }
        .left {
            text-align: center;

        }
        .lr_block {
            margin-bottom: 10px;
        }
        @media (max-width:810px) {
            .left,
            .right {
                float: none;
                width: 100%;
            }
        }
        h2 {
            text-transform: uppercase;
            font-size: 24px;
        }
        p {
            font-size: 16px;
        }
    }

    .buy {
        &_block {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            padding-top: 37px;
        }
        &_item {
            display: flex;
            @media (min-width:750px) {
                width: 48%;
                margin-bottom: 40px;
            }
            @media (min-width:550px) {
                padding-bottom: 15px;
            }

            i {
                margin-right: 20px;
                font-size: 65px;
            }
        }
    }

    .bread {
        color: $lblue;
        font-size: 14px;
        padding: 25px 0 22px 0;
        @media(max-width:600px) {
            background: #f3f3fb;
            margin-bottom: 20px;
            .wrapper {
                a {
                    display: none;
                    &:last-of-type {
                        display: block;
                        text-decoration: none;
                        color: $blue;
                        &:after {
                            display: none;
                        }
                        &:before {
                            content: "\e90a";
                            font-family: 'icomoon' !important;
                              speak: none;
                              font-style: normal;
                              font-weight: normal;
                              font-variant: normal;
                              text-transform: none;
                              line-height: 1;

                              /* Better Font Rendering =========== */
                              -webkit-font-smoothing: antialiased;
                              -moz-osx-font-smoothing: grayscale;

                            font-size: 12px;
                            margin-right: 10px;
                            color: #b4b9c5;
                        }
                    }
                }
                span {
                    display: none;
                }
            }

        }
        a {
            color: $lblue;
            position: relative;
            padding-right: 15px;
            &:hover {
                text-decoration: none;
            }
            &:after {
                content: '';
                display: block;
                position: absolute;
                width: 4px;
                height: 4px;
                border-radius: 50%;
                top: 50%;
                margin-top: -2px;
                right: 5px;
                background: $lblue; 
            }
        }
    }

    .category {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        flex-wrap: wrap;
        padding-bottom: 30px;
        @media (max-width:768px) {
            display: block;
        }
        .lr_block_flex {
            align-items: stretch;
        }
        &_item {
            width: 48%;
            background: url(../images/bg/cat.png) #fff no-repeat top right;
            background-size: contain;
            border: 1px solid #c5cad5;
            color: $blue;
            text-decoration: none;
            margin-bottom: 50px;
            box-shadow: 3px 3px 7px rgba(0,0,0,0.1);
            @media (max-width:768px) {
                width: 100%;
            }
            img {
                display: block;
                @media (max-width:600px) {
                    max-height: 200px;   
                }
            }
            &:not(:last-child) {
                .right {
                    display: flex;
                    justify-content: center;
                    align-items: flex-end;
                    padding-top: 20px;
                }
            }
            &:last-child {
                .right {
                    display: flex;
                    justify-content: flex-end;
                    align-items: flex-start;
                }
            }
            .left {
                padding-left: 43px;
                padding-top: 60px;
                position: relative;
                z-index: 2;
                @media (max-width:600px) {
                    padding-left: 20px;
                    padding-top: 20px;
                }
            }
            .right {
                position: relative;
                z-index: 1;
            }
            h2 {
                padding-top: 45px;
                padding-bottom: 50px;
                @media (max-width:600px) {
                    font-size: 16px;
                    padding: 20px 0;
                }
            }
        }
    }

    .filter {
        padding: 27px 25px 10px 25px;
        background: #eceff6;

        border: 1px solid #c5cad5;
        box-shadow: 3px 3px 7px rgba(0,0,0,.1);

        @media (max-width:1024px) {
            box-shadow: none;
            background: none;
            border: none;
        }
        &_block {
            margin-bottom: 20px;
            color: $blue;
            &_name {
                color: #757f94;
                font-size: 14px;
                margin-bottom: 7px;
                user-select: none;
                @media (max-width:500px) {
                    display: none;
                }
                label {
                    display: inline-block;
                    vertical-align: middle;

                    cursor: pointer;
                }

            }
            input {
                &[type=text] {
                    border: 1px solid #e6e9f1;
                    color: $blue;
                    padding: 11px 0;
                    text-align: center;
                }
            }
            &_btn {
                text-align: center;
            }
            .btn {
                margin: 10px 10px 20px 0;
                position: relative;
                input {
                    position: absolute;
                    opacity: 0;
                    z-index: 3;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    top: 0;
                    height: 100%;
                    width: 100%;
                }
            }
        }
        &_color {
            input {
                display: none;
                & + span {
                    display: inline-block;
                    width: 35px;
                    height: 35px;
                    border-radius: 50%;
                    border: 6px solid #eceff6;
                    //box-shadow: 0 0 0 1px #e7eaf2 inset;
                }
                &:checked {
                    & + span {
                        box-shadow: none;
                        border-color: #fff;
                    }
                }
            }
            label {
                cursor: pointer;
            }
        }
        
        &_reset {
            color: #757f94;
            font-weight: 400;
            font-style: italic;
            background: none;
            text-decoration: underline;
            &:hover {
                text-decoration: none;
            }
        }
        &_btn {
            width: 36px;
            height: 36px;
            border-radius: 50%;
            min-width: 36px;
            min-height: 36px;
            @include bg_gr;
            text-align: center;
            padding-top: 7px;
            color: #fff;
            font-size: 20px;
        }
    }
    .slide {
        &_input {
            display: flex;
            justify-content: space-between;
            align-items: center;
            input {
                width: 100%;
            }
            span {
                padding: 0 2px;
                font-weight: 700;
                font-size: 20px;
            }
        }
    }
    .aside {
        &_block {
            margin-bottom: 30px;
            @media (max-width:1024px) {
                margin-bottom: 0;
            }
        }
        &_menu_close {
            text-align: right;
            padding: 15px 15px 0 0;
        }
    }

    .collection {
        @media (max-width:645px) {
            width: calc( 100% + 30px );
            margin-left: -15px;
            margin-right: -15px;
            border: 10px solid #f3f3fb; 
            padding: 10px;
            margin-bottom: 15px;
        }
        .btn {
            text-decoration: none;
            padding-left: 5px;
            padding-right: 5px;
            &:hover {
                @include bg_gr;
                span {
                    color: #fff;
                }
            }
            span {
                color: $blue;
                font-weight: 700;
                white-space: normal;
            }
            @media (max-width:645px) {
                transform: none;
                border-radius: 45px;
                padding: 5px 15px;
                background: #f3f3fb;
                box-shadow: none;
                span {
                    transform: none;
                    text-transform: capitalize;
                    font-size: 14px;
                }
            }
        }
        &_img {
            text-decoration: none;
            text-align: center;
            @media (max-width:645px) {
                display: none!important;
            }
        }
        a {
            display: block;
        }
        &_block {
            display: flex;
            flex-wrap: wrap;
        }
        &_item {
            width: calc( 33% - 50px );
            margin: 0 25px 40px 25px;
            @media (max-width:645px) {
                width: auto;
                display: inline-block;
                padding: 0;
                margin: 0 10px 10px 0 ;
            }
            &:hover {
                .btn {
                    @include bg_gr;
                    span {
                        color: #fff;
                    }  
                }

            }
        }
        h2 {
            text-transform: uppercase;
            font-size: 18px;
            margin-bottom: 30px;
            @media (max-width:645px) {
                margin-bottom: 25px;
                padding-top: 15px;
                text-transform: inherit;
                font-weight: 400;
                font-size: 20px;
            }
        }
    }

    .sort {
        font-size: 16px;
        color: #757f94;

        display: flex;
        justify-content: space-between;

        background: #eceff6;
        padding: 6px 15px;

        margin-bottom: 19px;
        user-select: none;
        @media (max-width:700px) {
                display: none;
            }
        &_left {
            display: flex;
            justify-content: space-between;

            align-items: center;
            div {
                margin-right:15px;
                display: flex;
                &:hover {
                    a {
                        &:first-child {
                            color: $blue;
                        }
                        &:hover {
                            color: $blue;
                        }
                    }
                }
            }
            a {
                color: #757f94;
                text-decoration: none;
                display: inline-block;
                vertical-align: middle;
                margin-right: 4px;
                i {
                    font-size: 7px;
                }
                &.active {
                    color: $blue;
                    &:first-child {
                        text-decoration: underline;
                    }

                }
                &:last-child {
                    transform: rotate(-180deg);
                    i {
                        margin-top: 4px;
                        display: block;
                    }
                }
            }
        }
        &_right {
            display: flex;
            white-space: nowrap;
        }
        &_dd {
            position: relative;
            margin-left: 7px;
            &_block {
                position: absolute;
                top: 100%;
                display: none;
                background: #eceff6;
                padding: 2px;
                z-index: 1;
                left: -10px;
                box-shadow: 0 0 1px rgba(0,0,0,0.1);
                a {
                    display: block;
                    text-decoration: none;
                    padding: 3px 10px;
                    color: $blue;
                    font-weight: 400;
                    &:hover {
                        background: #fff;
                        font-weight: 700px;
                    } 
                }
            }
            i {
                font-size: 7px;
                color: #b2b9c4;
                padding-left: 5px;
            }
            &_head {
                display: flex;
                align-items: center;
                cursor: pointer;
                span {
                    color: $blue;
                    text-decoration: underline;
                    font-weight: 700;
                }
            }
            &:hover {
                & > div {
                    &:last-child {
                        display: block;
                    }
                }
                i {
                    color: $blue;
                }
            }
        }
        &_mob {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-bottom: 15px;
            display: none;
            @media (max-width:700px) {
                display: flex;
            }
            .jq-selectbox {
                max-width: 250px;
            }
            .jq-selectbox__select {
                border-radius: 45px;
            }
        }
    }

    .services {
        &_head {
            background: url(../images/bg/serv.png) repeat-x center;
            margin-bottom: 55px;
            @media (max-width:550px) {
                margin-bottom: 15px;
            }
            .wrapper {
                display: flex;
                @media (max-width:800px) {
                    //display: block;
                    flex-wrap: wrap;
                    justify-content: center;
                }
