                display: flex;
                align-items: flex-start;
                border-bottom: 1px solid #e6e8e9;
                position: relative;
            }
        }
        &_img {
            border: 1px solid #eceff6;
            padding-top: 6px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 118px;

            margin-right: 25px;
            @media (max-width:400px) {
                min-width: 115px;
                margin-right: 7px;
            }
        }
        &_name {
            font-size: 18px;
            font-weight: 500;
            color: $blue;
            text-decoration: none;
            margin-bottom: 15px;
            display: block; 
            margin-right: 30px;
        }
        &_remove {
            text-decoration: none;
            position: absolute;
            color: $orange;
            right: 10px;
            top: 5px;
            &_installation {
                width: 16px;
                height: 16px;
                display: inline-block;
                vertical-align: middle;
                background: $orange;
                border-radius: 50%;
                position: relative;
                i {
                    display: block;
                    position: absolute;
                    top: 50%;
                    width: 8px;
                    height: 1px;
                    background: #fff;

                    margin-top: -1px;
                    left: 50%;
                    margin-left: -4px;
                }
            }
        }
        &_info {
            color: #848a94;
            font-size: 16px;
            margin-bottom: 16px;
            @media (max-width:400px) {
                font-size: 13px;
            }
            & > div {
                margin-bottom: 5px;
            }
            span {
                color: #0d1a2b;
            }
        }
        &_color {
            display: inline-block;
            width: 20px!important;
            height: 20px; 
            min-width: 20px;
            min-height: 20px;

            border-radius: 50%;
            vertical-align: middle;
            margin-left: 4px;

        }
        &_cost {
            color: #0d1a2b;
            font-size: 20px;
            b {
                font-weight: 700;
                display: inline-block;
            }
        }
        &_bottom {
            width: 100%;

            display: flex;
            justify-content: space-between;
            align-items: center;
            margin-bottom: 15px;
            @media (max-width:450px) {
                display: block;
                .add_number {
                    margin-bottom: 15px;
                }
            }
        }
        &_control {
            width: 100%;
        }
        &_installation {
            display: flex;
            padding: 20px 0;
            justify-content: space-between;
            align-items: center;
            @media (max-width:400px) {
                .cart_cost {
                    margin-right: 5px;
                }
            }
            & > b {
                margin-left: 121px;
                @media (max-width:450px) {
                    margin-left: 0;
                }
            }
        }
        &_sum {
            background: #eceff6;
            display: flex;
            padding: 16px 20px;
            justify-content: space-between;
            align-items: center;
            border: 1px solid #b8c1d6;
            margin-bottom: 32px;
            @media (max-width:400px) {
                width: calc( 100% + 30px );
                margin-left: -16px;
                margin-right: -15px;
                border-left: none;
                border-right: none;
            }
            &_all {
                display: flex;
                justify-content: space-between;
                align-items: center;
                font-size: 24px;
                font-weight: 500;
                padding: 0 5px;
                background: url(../../images/bg/cart.png) repeat-x left bottom;
                @media (max-width:400px) {
                    font-size: 16px;  
                }

                b {
                    font-size: 24px;
                    @media (max-width:400px) {
                        font-size: 19px;  
                    }
                }
            }
        }
    }

    .pop {
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: 100;

        /* display: flex; */
        /* justify-content: center; */
        /* align-items: center; */
        text-align: center;
        overflow: auto;

        padding: 7px 15px;
        &:before {
            content: "";
            display: inline-block;
            height: 100%;
            margin-right: -.25em;
            vertical-align: middle;
            width: 0;
        }
        &_cont {
            background: #f4f4fb;
    //        padding: 45px 30px;
            position: relative;
            display: inline-block;
            vertical-align: middle;
            text-align: left;
            z-index: 2;

            width: 100%;
            max-width: 610px;
        }
        form {
            margin-top: 0;
            &:before {
                display: none;
            }
        }
        &_close {
            position: absolute;
            top: 15px;
            right: 13px;
            text-decoration: none;
            color: $blue;
            &:hover {
                color: $orange;
            }
        }
        &_bg {
            background: rgba(13, 26, 43, .61);
            position: fixed;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            z-index: 1;
        }
        &_block {
            box-shadow: 0 3px 13px rgba(0,0,0,.54);
        }
        &_cart {
            width: 100%;
            max-width: 610px;
            padding: 45px 30px 27px 30px;
    //        width: 100%;
            .cart {
                &_sum {
                    width: calc( 100% + 60px );
                    margin-left: -30px;
                    margin-right: -30px;
                    border-left: 0;
                    border-right: 0;
                }
                &_installation {
                    border-top: 1px solid #dce0e8;
                }
                &_item {
                    margin-bottom: 25px;
                }
            }
            form,
            .form {
                background: none;
                padding: 0;
                position: relative;
                margin-top: 0px;
                &:before {
                    display: none;
                }
                &_btn {
                    justify-content: space-between;
                    @media (max-width:550px) {
                        flex-direction: column;
                    }
                }
                &_agreement {
                    width: 100%;
                }
            }
            .add_number {
                background: #fff;
            }
            .master {
                margin-bottom: 0;
            }
            button[type="submit"] {
                min-width: 222px;
            }
            @media (max-width: 500px) {
                .form_row_flex {
    //                display: block;
                    &  > div {
                        width: 100%;
                    }
                }
            }
            @media (max-width: 400px) {
                .cart {
                    &_img {
                        min-width: 75px;
                    }
                    &_item {
                        .cart_cost {
                            min-width: auto;
                        }
                    }
                    &_installation {
                        width: calc( 100% + 60px);
                        margin-left: -30px;
                        margin-right: -30px;
                        padding-left: 15px;
                    }
                }
            }
        }
    }
    .header_search form {
        background: none;
        margin: 0;
        padding: 0;
        &:before {
            display: none;
        }
    }
    .product-item-container {
        .goods_item {
            width: 100%;
        }
    }
    .after_h2_cont {
        padding: 25px 0;
    }
}
.pop {
    display: none;
}