@import "variables";
@import "mixins";
@import "flex";
@import "reset";
@import "base";
@import "layout";

@import "modules/all";
@import "components/all";

@import "media";

@font-face {
  font-family: 'HelveticaNeueCyr-Roman';
  src: url('../fonts/HelveticaNeueCyr-Roman.woff') format('woff'),
       url('../fonts/HelveticaNeueCyr-Roman.ttf') format('truetype'),
       url('../fonts/HelveticaNeueCyr-Roman.svg#HelveticaNeueCyr-Roman') format('svg');
  src: url('../fonts/HelveticaRegular.eot');
  src: url('../fonts/HelveticaRegular.woff2') format('woff2'),
       url('../fonts/HelveticaRegular.eot?#iefix') format('embedded-opentype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'HelveticaBold';
  src: url('../fonts/HelveticaBold.eot');
  src: url('../fonts/HelveticaBold.woff2') format('woff2'),
       url('../fonts/HelveticaBold.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/HelveticaNeueCyr-Bold.woff') format('woff'),
       url('../fonts/HelveticaNeueCyr-Bold.ttf') format('truetype'),
       url('../fonts/HelveticaNeueCyr-Bold.svg#HelveticaNeueCyr-Bold') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'HelveticaLight';
  src: url('../fonts/HelveticaLight.eot');
  src: url('../fonts/HelveticaLight.woff2') format('woff2'),
       url('../fonts/HelveticaLight.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/HelveticaNeueCyr-Light.woff') format('woff'),
       url('../fonts/HelveticaNeueCyr-Light.ttf') format('truetype'),
       url('../fonts/HelveticaNeueCyr-Light.svg#HelveticaNeueCyr-Light') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'HelveticaMedium';
  src: url('../fonts/HelveticaMedium.eot');
  src: url('../fonts/HelveticaMedium.woff2') format('woff2'),
       url('../fonts/HelveticaMedium.eot?#iefix') format('embedded-opentype');
  src: url('../fonts/HelveticaNeueCyr-Medium.woff') format('woff'),
       url('../fonts/HelveticaNeueCyr-Medium.ttf') format('truetype'),
       url('../fonts/HelveticaNeueCyr-Medium.svg#HelveticaNeueCyr-Medium') format('svg');
  font-weight: normal;
  font-style: normal;
}

@import url('https://fonts.googleapis.com/css?family=Rubik:300,300i,400,400i,500,500i,700,700i,900,900i&subset=cyrillic');
