
header {
    background: #eceff6;
    padding-top: 31px;
        padding-bottom: 22px;
    
    z-index: 5;
    position: relative;
    @media (max-width:965px) {
        background: #fff;
        padding-bottom: 0px;
    }
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        @media (max-width:965px) {
            & > * {
                padding-bottom: 25px;
            }   
        }
    }
}
.header {
    &_search {
        position: relative;
        @media (max-width:965px) {
            
            order: 5;
            background: #eceff6;
            padding: 10px!important;
            margin-left: -15px;
            margin-right: -15px;
            width: calc( 100% + 30px );
            display: none;
            input {
                &[type="text"] {
                    width: 100%;
                }
            }
            button {
                padding-right: 25px!important;
            }
        }
        input {
            &[type="text"] {
                border: 1px solid #dadee6;
                border-radius: 45px;
                box-shadow:inset 0 0 7px rgba(0,0,0,0.16);
                padding: 14px 45px 14px 22px;
                font-size: 16px;
                @include plhl {
                    color: #95a0ad;
                    font-weight: 300;
                    font-style: italic;
                }
            }
        }
        button {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            font-size: 25px;
            padding-right:16px;
            background: none;
            border: none;
            display: flex;
            justify-content: center;
            align-content: center;
        }
        &_mob {
            font-size: 25px;
            display: none;
            cursor: pointer;
            @media (max-width:965px) {
                display: block;
            }
        }
    }
    &_block {
        display: flex;
        justify-content: space-between;
        align-items: center;
        
        @media (max-width:550px) {
            &:last-child {
                display: none;
            }
        }
        i {
            &[class^="icon-"] {
                background: -webkit-linear-gradient(#f9b701, #fb5a09);
              -webkit-background-clip: text;
              -webkit-text-fill-color: transparent;
                font-size: 45px;
                margin-right: 11px;
            }
        }
        a {
            display: block;
        }
    }
    &_call {
        font-size: 16px;
        color: #1e3046;
        text-decoration: none;
        font-weight: 500;
        span {
            display: inline-block;
            border-bottom:2px dashed  #1e3046;
        }
        &:hover {
            span {
                border-color: rgba(0,0,0,0);
            }
        }
        & + span {
            font-weight: 300;
            font-style: italic;
            font-size: 14px;
        }
        
        &_mob {
            display: none;
            font-size: 29px;
            cursor: pointer;
            @media (max-width:750px) {
                display: block;
                & + div {
                    display: none;
                    order: 5;
                    background: #eceff6;
                    padding: 20px;
                    margin-left: -15px;
                    margin-right: -15px;
                    width: calc( 100% + 30px );
                    justify-content: center;
                    i {
                        display: none;
                    }
                    div {
                        @include bg_gr;
                        padding: 15px;
                        text-align: center!important;
                        span {
                            font-weight: normal!important;
                            border: none!important;
                            
                            color: #fff;
                            font-style: normal;
                            font-weight: 500;
                            font-size: 25px;
                            
                        }
                        .header_call {
                            & + span {
                                font-size: 15px;
                            }
                        }
                    }
                }
            }
        }
    }
    &_phone {
        color: #2d2d2d;
        font-size: 20px;
        text-decoration: none;
        &:hover {
            text-decoration: underline;
        }
    }
    &_back_call {
        color: #fb4110;
        font-size: 14px;
        font-weight: 300;
        text-decoration: none;
        font-style: italic;
        &:hover {
            text-decoration: underline;
        }
    }
    &_bottom {
        display: flex;
    }
    &_cart {
        background: #1e3046;
        min-width: 210px;
        flex-basis: 210px;  
        position: relative;
        color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: flex-start;
        font-size: 16px;
        padding-left: 15px;
        @media (max-width:550px) {
            display: none;
        }
        a {
            color: #ffffff;
            text-decoration: none;
            position: relative;
            font-size: 20px;
            margin-right: 18px;
            span {
                position: absolute;
                width: 15px;
                height: 15px;
                display: block;
                border-radius: 50%;
                background: rgb(244,106,23);
                background: -moz-linear-gradient(45deg, rgba(244,106,23,1) 0%, rgba(249,174,2,1) 100%);
                background: -webkit-linear-gradient(45deg, rgba(244,106,23,1) 0%,rgba(249,174,2,1) 100%);
                background: linear-gradient(45deg, rgba(244,106,23,1) 0%,rgba(249,174,2,1) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f46a17', endColorstr='#f9ae02',GradientType=1 );
                color: #fff;
                font-size: 9px;
                font-weight: 700;
                text-align: center;
                padding-top: 3px;
                
                top: -5px;
                right: -10px;
            }
        }
        &_top {
            display: none;
            text-decoration: none;
            color: $blue;
            
            position: relative;
            &_next {
                order: 5;
                width: 100%; 
                display: none;
                
                
                position: relative;
//                justify-content: space-between;
                padding: 15px!important;
                background: #eceff6;
                
                margin-left: -15px;
                margin-right: -15px;
                width: calc( 100% + 30px);
                
                & > div {
                    display: flex;
                    align-items: flex-start;
                }
                @media (max-width:550px) {
//                    display: flex;
                }
                i[class^="icon-"] {
                    width: 35px;
                    height: 35px;
                    min-width: 35px;
                    border: 1px solid $blue;
                    border-radius: 50%;
                    font-size: 20px!important;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    -webkit-text-fill-color: $blue;
                    -webkit-background-clip: unset;
                    background: none;
                    position: absolute;
                    top: 50%;
                    
                    margin-top: -17.5px;
                    right: 10px;
                }
                a {
                    color: $blue;
                    text-decoration: none;
                }
            }
            &_block {
                color: #b7babf;
                font-weight: 500;
                font-size: 12px;
                & + & {
                    margin-left: 25px;
                }
                a {
                    font-size: 16px;

                    text-decoration: none;
                    
                    display: block;
                    padding-top: 7px;
                }
            }
            @media (max-width:550px) {
                display: block;
            }
            
            i {
                font-size: 29px;
                
            }
            span {
                position: absolute;
                width: 20px;
                height: 20px;
                display: block;
                border-radius: 50%;
                @include bg_gr;
                color: #fff;
                font-size: 12px;
                font-weight: 700;
                text-align: center;
                padding-top: 3px;
                top: -5px;
                right: -10px;
                        }
        }
//        &:after {
//            content: '';
//            display: block;
//            position: absolute;
//            top: 0;
//            bottom: 0;
//            right: -500%;
//            left: 100%;
//            background: #1e3046;
//        }
    }
}

nav {
    background: #0d1a2b;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    width: 100%;
    padding-right: 15px;
        z-index: 1;
    
    @media (max-width:550px) {
        padding-right: 0;
    }
    a {
        text-decoration: none;
        
        display: inline-block;
        font-size: 16px;
        color: #d2d8e8;
    }
    & > a {
        padding-top: 21px;
        padding-bottom: 21px;
        &:hover {
            color: #fa9104;
            font-weight: 700;
        }
    }
}
.nav {
    &_catalog {
        @media (max-width:550px) {
            width: 100%;
            margin: 0;
        }
        &_a {
            i {
                font-size: 25px;
                color: #fff;
                display: none;
                @media (max-width:550px) {
                    display: inline-block;
                    margin-right: 15px;
                }
            }
        }
        & > a {
            font-weight: 700;
            display: flex;
            align-items: center;
                padding-top: 18px;
                padding-bottom: 18px;
            white-space: nowrap;
            margin-right: 20px;
            b {
                display: none;
            }
            @media (max-width:550px) {
                justify-content: center;
                font-size: 18px;
                margin: 0;
                b {
                    display: inline-block;
                    padding-left: 5px;
                }
            }
        }
        
        &_span {
            display: inline-block;
            vertical-align: middle;
            width: 13px;
            height: 13px;
            margin-right: 13px;
            position: relative;
            @media (max-width:550px) {
                display: none;
            }
        }
        i:not([class^="icon-"]) {
            
            width: 13px;
            height: 2px;
            
            display: block;
            background: #fb4110;
            + i {
                margin-top: 3px;
            }
        }
        &:hover {
            .nav_catalog_span i {
                background: rgb(251,108,7);
                background: -moz-linear-gradient(left, rgba(251,108,7,1) 0%, rgba(249,183,1,1) 100%);
                background: -webkit-linear-gradient(left, rgba(251,108,7,1) 0%,rgba(249,183,1,1) 100%);
                background: linear-gradient(to right, rgba(251,108,7,1) 0%,rgba(249,183,1,1) 100%);
                filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#fb6c07', endColorstr='#f9b701',GradientType=1 );
            }
            & > a {
                color: #fa9104; 
                & + div {
                    display: block;
                }
            }
            .nav_catalog_span {
                &:after {
                    content: '';
                    width: 0;
                    height: 0;
                    border: 27px solid transparent;
                    border-bottom-color: #fff;
                    bottom: -34px;
                    position: absolute;
                    margin-left: -20px;
                }
            }
        }
        &_dd {
            position: absolute;
            display: none;
            left: 0;
            right: 0;
            background: #fff;
            border-bottom: 5px solid $orange;
            
            .wrapper {
                display: flex;
                justify-content: space-between;
                align-items: flex-start;
                @media (max-width:765px) {
                
                }
                @media (max-width:765px) {
                    display: block;
                    padding: 0 !important;
                }
            }
        }
        &_menu {
            &:hover {
                & > a {
                    color: #fa9104;
//                    font-weight: 700;
                    text-decoration: none;
                    position: relative;
                    &:after {
                        content: '';
                        width: 0;
                        height: 0;
                        border: 27px solid transparent;
                        border-bottom-color: #fff;
                        bottom: -11px;
                        position: absolute;
                        /* margin-left: -20px; */
                        left: 0px;
                    }
                    & + div {
                        display: block;
                    }
                }
            }
            &_dd {
                display: none;
                position: absolute;
                padding: 26px 23px;
                background: #fff;
                min-width: 245px;
                margin-left: -15px;
                a {
                    display: block;
                    text-decoration: underline;;
                    color: $blue;
                    margin-bottom: 15px;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
            
            & > a {
                padding-top: 18px;
                padding-bottom: 19px;
            }
            & ~ a {
                &:hover {
                    color: #fa9104;
                }
                
            }
            
        }
        &_block {
            padding-top: 50px;
            padding-left: 50px;
            width: 33%;
            @media (max-width:765px) {
                width: 100%;
                padding-top: 22px;
                padding-left: 15px;
            }
            @media (max-width:550px) {
                padding: 0;
                width: 100%;
                background: $blue;
            }
            a {
                color: #1e3046; 
                text-decoration: underline;
                display: block;
                margin-bottom: 15px;
                @media (max-width:765px) {
                    display: inline-block;
                    margin-bottom: 5px;
                    margin-right: 5px;
                }
                &:hover {
                    text-decoration: none;
                }
                @media (max-width:550px) {
                    margin: 0;
                    padding: 15px 20px;
                    color: #fff;
                    text-transform: uppercase;
                    font-size: 18px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    &:hover {
                        text-decoration: none;
                    }
                }
            }
        }
        &_head {
            position: relative;
            font-size: 18px;
            padding-bottom: 9px;
            margin-bottom: 18px;
            @media (max-width:765px) {
                margin-bottom: 0;
            }
            @media (max-width:550px) {
                padding: 0;
                margin: 0;
                img {
                    display: none;
                }
            }
            a {
                text-decoration: none;
                &:hover {
                    text-decoration: underline;
                    @media (max-width:550px) {
                        text-decoration: none;
//                        background: $orange;
                        @include bg_gr;
                    }
                }
            }
            img {
                position: absolute;
                top: -21px;
                right: calc( 100% + 20px );
            }
            b {
                & + i {
                    display: none;
                    @media (max-width:550px) {
                        display: block;
                    }
                }
            }
        }
        &_list {
            display: flex;
            justify-content: space-between;
            @media (max-width:765px) {
                display: block;
                padding-bottom: 15px;
            }
            @media (max-width:550px) {
                display: none;
            }
            span {
                width: 60%;
                & + span {
                    width: 40%;
                }
                @media (max-width:765px) {
                    width: auto;
                    & + span {
                        width: auto;
                    }
                }
            }
            
        }
        &_go {
            font-style: italic;
            font-weight: 300;
            @media (max-width:550px) {
                display: none;
            }
            a {
                text-decoration: none;
                &:hover {
                    i {
                        margin-left: 20px;
                    }
                }
            }
            span {
                text-decoration: underline;
                &:hover {
                    text-decoration: none;
                }
            }
            i {
                margin-left: 15px;
                transition: all ease 0.4s;
                color: $orange;
            }
        }
    }
    &_section {
        background: #1e3046;
        position: relative;
        z-index: 5;
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 0;
            bottom: 0;
            right: 50%;
            left: -500%;
            background: #0d1a2b;
            @media (max-width:550px) {
                left: 0;
                right: 0;
            }
        }
    }
    &_menu {
        
        width: 100%;
        & > div:not(.nav_menu_mobVis) {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding-right: 5%;
            padding-left: 28px;
            @media (max-width:990px) {
                padding-right: 2%;
                padding-left: 15px;
            }
            @media (max-width:930px) {
                display: block;
            }
        }
        &_close {
            text-align: right;
            display: none;
            padding: 0!important;
            ~ .nav_menu_mobVis {
                height: 100%;
                @media (max-width:930px) {
                    display: flex;
                    justify-content: flex-end;
                    flex-direction: column;
                }
            }
        }
        @media (max-width:930px) {
            position: fixed;
            
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            
            top: 0;
            bottom: 0;
            width: 320px;
            left: -330px;
            z-index: 50;
            background: #fff;
            padding: 10px;
            box-shadow: 0 0 2px rgba(0,0,0,.1);
            
            transition: left ease 0.4s;
            z-index: 60;
            &.open {
                left: 0;
            }
            & > * {
                width: 100%;
            }
            &_close {
                display: block;
            }
            .nav_catalog_menu {
                border-bottom: 1px solid #d2d6da;
                &:hover > a:after {
                    display: none!important;
                }
                &_dd {
                    display: none!important;
                }
            }
            a {
                color: #000;
                padding: 15px;
                display: block;
                & + a {
                    border-top: 1px solid #d2d6da;
                }
            }
        }
        &_mobVis {
            display: none;
            @media (max-width:930px) {
                display: block;
            }
        }
        &_tel {
            font-size: 30px;
        }
    }
}

